@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap);
* {
    font-family: Arial, Helvetica, sans-serif;
}
body{
    margin: 10px;
}
input{
    font-size: 1.2rem;
    width: 100%;
}
.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
}
.card p{
    padding: 5px;
    text-align: center;
}
.card img{
    max-height: 140px;
}
